import React, {Component} from 'react';
import {Container, Row, Col} from 'react-bootstrap';
import {getLogo} from '../service';
import TeamCard from './TeamCard';
import axios from 'axios';
import ClipLoader from 'react-spinners/ClipLoader';


class Team extends Component{
    constructor(props){
        super(props);
    
        this.state = {
            players: [],
            team: {},
            teamId: '',
            loading: true,
            trophy: '',
            captain: '',
        }
    }

    componentDidMount(){
        const {match: {params}} = this.props;
        this.setState({teamId: params.id});
        const proxy_url = 'https://cors-anywhere.herokuapp.com/';
        const url = 'https://ipl-t20.herokuapp.com/teams/'+params.id;
        axios.get(proxy_url+url).then(response=>{
            console.log(response);
            for(let i=0; i<response.data.players.length; i++){
                if(response.data.players[i].id === response.data.team.captainId){
                    this.setState({captain: response.data.players[i].name});
                }
            }
            this.setState({players: response.data.players, team: response.data.team, loading: false});
        }).catch(error=>{
            console.log('error=>',error);
        })
    }

    getTeamName(teamid){
        return localStorage.getItem(teamid+'teamName');
    }
    getVenue(teamId){
        return localStorage.getItem(teamId+'venue');
    }

    isWicketKeeper(playerId){
        if(playerId === this.state.team.wicketKeeperId)
            return true;
        else
            return false;
    }

    isCaptain(playerId){
        if(playerId === this.state.team.captainId)
            return true;
        else
            return false;
    }

    render(){
        let trophy = '';
        const years = localStorage.getItem(this.state.teamId+'winnings'); 
        if(years){
            trophy = <span className="trophies" style={{background: 'none'}}> <i className="fa fa-trophy"></i> {years}</span>;
        }
        var cards = [];
        for(let player of this.state.players){
            cards.push(
                <Col md={4} lg={3} sm={12} style={{paddingLeft:'4px', paddingRight:'4px'}}>
                    <TeamCard
                        key={player.id}
                        name={player.name}
                        id={this.state.teamId}
                        image = {player.image}
                        stats = {player.stats}
                        nationality = {player.nationality}
                        isCaptain = {this.isCaptain(player.id)}
                        isWicketKeeper = {this.isWicketKeeper(player.id)}
                    >   
                    </TeamCard>
                </Col>
            )
        }
        return(
            <div>
                <div className='banner'>
                <div className={this.state.teamId+' layer'}></div>
                    <Row>
                        <Col md={3} className='logo-container'>
                            <img src={getLogo(this.state.teamId)}></img>
                        </Col>
                        <Col md={5} className='team-details'>
                            <h3>{this.getTeamName(this.state.teamId)}</h3>
                            <p> {trophy} </p>
                            <Row>
                                <Col md={6}>
                                    <span className='venue'>Venue</span>
                                    <span className='venue-value'>{this.getVenue(this.state.teamId)}</span>
                                </Col>
                                <Col md={6}>
                                    <span className='venue'>Captiain</span>
                                    <span className='venue-value'>{this.state.captain}</span>
                                </Col>
                            </Row>
                        </Col>
                    </Row>
                </div>
                <Container>
                    <div className="sweet-loading">
                        <ClipLoader
                            size={150}
                            color={"#123abc"}
                            loading={this.state.loading}
                        />
                    </div>
                    <Row style={{paddingTop: '20px'}}>
                        {cards}
                    </Row>
                </Container>
            </div>
        )
    }
}

export default Team;