import csk_logo from '../assets/logos/csk.png';
import dc_logo from '../assets/logos/dc.png';
import kxp_logo from '../assets/logos/kxp.png';
import rcb_logo from '../assets/logos/rcb.png';
import kkr_logo from '../assets/logos/kkr.png';
import mi_logo from '../assets/logos/mi.png';
import rr_logo from '../assets/logos/rr.png';
import srh_logo from '../assets/logos/srh.png';

import image  from '../assets/logos/srh.png';



export const getLogo = (id) => {
    if(id === 'chennai-super-kings'){
        return csk_logo;
    }else if(id === 'delhi-capitals'){
        return dc_logo;
    }else if(id === 'kings-xi-punjab'){
        return kxp_logo;
    }else if(id === 'royal-challengers-bangalore'){
        return rcb_logo;
    }else if(id === 'kolkata-knight-riders'){
        return kkr_logo;
    }else if(id === 'mumbai-indians'){
        return mi_logo;
    }else if(id === 'rajasthan-royals'){
        return rr_logo;
    }else if(id === 'sun-risers-hyderabad'){
        return srh_logo;
    }else{
        return image;
    }
}