import React, {Component} from 'react';
import { Card} from 'react-bootstrap';
import { Link } from 'react-router-dom';


class TeamsCard extends Component{

    constructor(props){
        super(props);
        this.state = {
            first_name: 'Unknown',
            last_name: 'Unknown'
        }
    }
    getLastName(name){
        let names = name.split(' ');
        let last_name = names[names.length -1 ];
        return last_name;
    }
    getFirstName(name){
        let names = name.split(' ');
        let first_name = "";
        for(let i=0; i< names.length - 1; i++){
            first_name += names[i]+' ';
        }
        return first_name;
    }

    render(){
        let trophies = <span className='trophies not'></span>
        if(this.props.winningYears.length > 0){
            trophies = <span className="trophies"><i className="fa fa-trophy"></i> {this.props.winningYears.join(', ')}</span>
        }
        return(
            <Card style={{textAlign: 'center'}} className={this.props.id}>
                <div className='img-container'>
                    <Card.Img variant="center" src={this.props.logo} style={{width: '100px', alignSelf: 'center'}}/>
                </div>
                <Card.Body>
                    <Card.Title className='mobile-name' style={{marginBottom: '0px'}}>{this.getFirstName(this.props.name)} </Card.Title>
                    <Card.Title className='mobile-name'> {this.getLastName(this.props.name)}</Card.Title>
                    <Card.Text className='mobile-venue' style={{fontSize: '12px'}}>{this.props.venue}</Card.Text>
                    <p>{trophies}</p>
                    <Link to={'/teams/'+this.props.id}>
                        <a className="teams-link" href="/teams">TEAM HOME</a>
                    </Link>
                </Card.Body>
            </Card>
        )
    }
}

export default TeamsCard;