import React, {Component} from 'react';
import { Card, Col, Row} from 'react-bootstrap';


class TeamCard extends Component{

    constructor(props){
        super(props);
        this.state = {
            first_name: 'Unknown',
            last_name: 'Unknown'
        }
    }
    getLastName(name){
        let names = name.split(' ');
        let last_name = names[names.length -1 ];
        return last_name;
    }
    getFirstName(name){
        let names = name.split(' ');
        let first_name = "";
        for(let i=0; i< names.length - 1; i++){
            first_name += names[i]+' ';
        }
        return first_name;
    }

    render(){
        let foreigner = '';
        let captain = '';
        let keeper = '';
        if(this.props.nationality !== "Indian"){
            foreigner = <span className='fa fa-plane player-icon'></span>
        }
        if(this.props.isCaptain){
            captain = <span className='fa fa-copyright player-icon'></span>
        }
        if(this.props.isWicketKeeper){
            keeper = <span className='fa fa-bars player-icon' style={{transform: 'rotate(90deg)'}}></span>
        }
        return(
            <Card style={{textAlign: 'center', flexDirection: 'column'}} className={this.props.id}>
                <div style={{textAlign: 'left', paddingLeft: '5px'}} className="player-icon-container">
                    {captain}
                    {keeper}
                    {foreigner}
                </div>
                
                <Card.Img className="player-image" variant="center" src={this.props.image} style={{alignSelf: 'center'}}/>
                <div className="player-info">
                    <Card.Text style={{marginBottom: '0px'}}>{this.getFirstName(this.props.name)}</Card.Text>
                    <Card.Title style={{marginTop: '1px'}}>{this.getLastName(this.props.name)}</Card.Title>
                    <Card.Text style={{fontSize: '12px'}}>
                        IPL 2019
                    </Card.Text>
                    <Row>
                        <Col className="border-right">
                            <div className="description-block">
                                <h6 className="description-header">{this.props.stats.matches}</h6>
                                <span className="description-text">Matches</span>
                            </div>
                        </Col>
                        <Col className="border-right">
                            <div className="description-block">
                                <h6 className="description-header">{this.props.stats.runs}</h6>
                                <span className="description-text">Runs</span>
                            </div>
                        </Col>
                        <Col className="border-right">
                            <div className="description-block">
                                <h6 className="description-header">{this.props.stats.wickets}</h6>
                                <span className="description-text">Wickets</span>
                            </div>
                        </Col>
                    </Row>

                    <a className="teams-link" style={{color: '#111111'}}>View Profile</a>
                </div>
            </Card>
        )
    }
}

export default TeamCard;