import React, {Component} from 'react';
import TeamsCard from './TeamsCard';
import {Container, Row, Col} from 'react-bootstrap';
import { getLogo } from '../service';
import axios from 'axios';

import ClipLoader from 'react-spinners/ClipLoader';


class Teams extends Component{

    constructor(props){
        super(props);
        this.state = {
            teams: [],
            loading: true
        }
    }

    componentDidMount(){
        axios.get('https://cors-anywhere.herokuapp.com/'+'https://ipl-t20.herokuapp.com/teams')
        .then(response=>{
            console.log(response);
            this.setState({teams: response.data, loading: false});
        })
        .catch(error=>{
            console.log('error=>',error, error.length);
            this.props.history.push('/error/:'+error);
        })
    }

    render(){
        var cards = [];
        for(var i=0; i< this.state.teams.length; i++){
            localStorage.setItem(this.state.teams[i].id+'teamName', this.state.teams[i].teamName);
            localStorage.setItem(this.state.teams[i].id+'winnings', this.state.teams[i].winningYears.join(', '));
            localStorage.setItem(this.state.teams[i].id+'venue', this.state.teams[i].venue);
            cards.push(
                <Col md={6} lg={3} sm={12} style={{paddingLeft:'4px', paddingRight:'4px'}}>
                    <TeamsCard
                        key={this.state.teams[i].id}
                        name={this.state.teams[i].teamName}
                        id={this.state.teams[i].id}
                        venue = {this.state.teams[i].venue}
                        winningYears = {this.state.teams[i].winningYears}
                        logo={getLogo(this.state.teams[i].id)}>   
                    </TeamsCard>
                </Col>
            )
        }

        return(
            <Container>
                <div className="sweet-loading">
                    <ClipLoader
                        size={150}
                        color={"#123abc"}
                        loading={this.state.loading}
                    />
                </div>
                <Row style={{paddingTop: '20px'}}>
                    {cards}
                </Row>
            </Container>
        )
    }
}

export default Teams;